/*quote-1, quote-2*/
.quote-1, .quote-2{
  /*font-family:'Poppins';*/
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-style:normal;
  font-size: 1.4em;

  position: relative;
  width:90%;
  margin:50px auto;

  color: #555555;
  padding:1.2em 30px 1.2em 75px;
  border-left:8px solid #90191b;
  line-height:1.6;
  
}

.quote-1::before, .quote-2::before{
  font-family: 'Raleway', sans-serif;
  content: "\201C";
  color:#90191b;
  font-size:4em;
  position: absolute;
  left: 10px;
  top:-10px;
}

.quote-1::after, .quote-2::after{
  content: '';
}

.quote-1 span, .quote-2 span{
  display:block;
  color:#333333;
  font-style: normal;
  font-weight: bold;
  margin-top:1em;
}

.quote1-i, .quote2-i{
